<script>
import axios from "axios";
export default {
   name: "home-classapan",
    data() {
    return{
        info_ClassA:"",
        info_Water:"",
        info_Kpan:"",
        info_WindAvg:"",
        info_HumiAvg:"",
        info_Kpan:"",
    };
  },
  mounted() {      
    axios
       .get(
        "https://script.google.com/macros/s/AKfycbx6AHT9dw-tMKMr33yzPJRQIEXoY_M0014eKcjbdHOAIjm1vDI/exec?action=getData"
        )
      .then((response) => {
        var data_CAP3 = response.data;
        var datestring =  new Date().getFullYear() + "-" +("0"+(new Date().getMonth()+1)).slice(-2) + "-" +("0" + (new Date().getDate()-1)).slice(-2);
        var test,datata,WaterMax,WaterMin,Kpan;
        var WindAvg=[],HumiAvg=[],water=[];
         
        for(var item of data_CAP3){
            this.info_data_CAP3 = item;
            test = this.info_data_CAP3.DateTime.lastIndexOf("T");
            datata=this.info_data_CAP3.DateTime.substring(0, test);
            if(datata==datestring){
                WindAvg.push(this.info_data_CAP3.Wind_3);
                HumiAvg.push(this.info_data_CAP3.Humi_3);
                water.push(this.info_data_CAP3.WaterLevel_3);
                }
        }
        console.log(this.info_data_CAP3);
        
  
         HumiAvg = HumiAvg.reduce((a, b) => a + b, 0) / HumiAvg.length;
         WindAvg = WindAvg.reduce((a, b) => a + b, 0) / WindAvg.length;
         WaterMax = Math.max(...water);
         WaterMin = Math.min(...water);
         water = WaterMax-WaterMin;

         if ((WindAvg < 2) && (HumiAvg < 40)) {Kpan = 0.65;}
         else if ((WindAvg < 2) && (HumiAvg > 40) && (HumiAvg < 70)) {Kpan = 0.75;}
         else if ((WindAvg < 2) && (HumiAvg > 70)) {Kpan = 0.85;}
         else if ((WindAvg > 2 && WindAvg < 5) && (HumiAvg < 40)) {Kpan = 0.60;}
         else if ((WindAvg > 2 && WindAvg < 5) && (HumiAvg > 40) && (HumiAvg < 70)) {Kpan = 0.70;}
         else if ((WindAvg > 2 && WindAvg < 5) && (HumiAvg > 70)) {Kpan = 0.75;}
         else if ((WindAvg > 5 && WindAvg < 8) && (HumiAvg < 40)) {Kpan = 0.55;}
         else if ((WindAvg > 5 && WindAvg < 8) && (HumiAvg > 40) && (HumiAvg < 70)) {Kpan = 0.60;}
         else if ((WindAvg > 5 && WindAvg < 8) && (HumiAvg > 70)) {Kpan = 0.65;}
         else if ((WindAvg > 8) && (HumiAvg < 40)) {Kpan = 0.45;}
         else if ((WindAvg > 8) && (HumiAvg > 40) && (HumiAvg < 70)) {Kpan = 0.55;}
         else if ((WindAvg > 8) && (HumiAvg > 70)) {Kpan = 0.60;}      
         
         this.ETo_C = (Kpan * water).toFixed(2);
         this.info_Water = water.toFixed(2);
         this.info_WindAvg = WindAvg.toFixed(2);
         this.info_HumiAvg = HumiAvg.toFixed(2);
         this.info_Kpan = Kpan.toFixed(2);

         
         console.log("WindAvg : "+ this.info_WindAvg);
         console.log("Temp : "+ this.info_data_CAP3.Temp_3);
         console.log("HumiAvg : "+ this.info_HumiAvg);
         console.log("Kpan : "+ this.info_Kpan);
         console.log("Epan : "+ this.info_Water);
         console.log("ETo CAP : "+this.ETo_C);

       }) 

    }
    }
</script>
